@use 'global' as *;

.p-works {
  
}

.p-works__text-block {
  margin-top: rem(30);
}

.p-works__text {
  text-align: center;
  font-size: rem(14);
  font-weight: 300;
  letter-spacing: 0.05em;
  line-height: 1.79;
}

.p-works__btn {
  text-align: center;
  margin-top: rem(30);
}

.p-works__text-body {
  background-color: #F0F0F0;
  width: 100vw;
  margin-left: calc(50% - 50vw);
  padding-bottom: rem(60);
  padding-top: rem(156);
  margin-top: rem(-116);
}

.p-works__images {
  position: relative;
}

.p-works__img {
  position: relative;
}

.p-works__img::before {
  content: "";
  display: block;
  padding-top: 68.955%;
}

.p-works__img img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}